.wpte-product-general-style-4{
    text-align: center;
    .wpte-general-layout-image-area{
        position: relative;
        overflow: hidden;
        .wpte-product-layouts-top-quick-view{
            .wpte-product-layouts-quick-view{
                position: absolute;
                top: 12px;
                right: 15px;
                z-index: 2;
                a{
                    width: 40px;
                    height: 40px;
                    color: #9e9e9e;
                    background: #ffffff;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 14px;
                    border-radius: 50px;
                    cursor: pointer;
                }
            }
        } 
    }
    .wpte-general-layout-product-thumb {
        line-height: 1;
     }
     .wpte-general-layout-product-button-icons{
        position: absolute;
        bottom: -50px;
        width: 100%;
        transition: all 0.3s;
        display: flex;
    }

    .wpte-general-layout-button-icons{
        width: 100%;
        a, .button{
            width: 100%;
            text-align: center;
            display: block;
            box-sizing: border-box;
            cursor: pointer;
            color: #8a8a8a;
            background: rgba(255,255,255,1.00);
            font-size: 16px;
            line-height: 1.8em;
            text-transform: none;
            font-weight: normal;
            border-style: solid;
            border-width: 0px 1px 1px 1px;
            border-color: #ededed;
            border-radius: 0px 0px 0px 0px;
            padding: 8px 0px 8px 0px;
            .wpte-icon{
                margin-right: 10px;
            }
        }
    }

    .wpte-general-layout-product-button-cart{
        span, a{
            display: block;
        }
        a{
            .wpte-cart-text,
            .wpte-variable-text,
            .wpte-groupde-text,
            .wpte-external-text,
            .wpte-view-cart-text{
                display: inline-block;
            }
        }
    }
    
    .wpte-general-layout-category-area{
        a{
            display: block;
        }
    }
    .wpte-general-layout-product-img{
        img{
            transition: transform .5s; /*Animation*/
        }
    }

    &:hover{
        .wpte-general-layout-product-button-icons{
            bottom: 0;
        }
        .wpte-general-layout-product-img{
            img{
                transform: scale(1.2);
            }
        }
    }

} 


